import { Component } from '@angular/core';
import { initializeApp } from 'firebase/app';
import { doc, getDocs, collection, getFirestore, updateDoc } from "firebase/firestore";


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title = 'crud-firebase';
  data: any
  modelData: any
  enableSave = false
  db: any
  password = ''
  safe = false

  constructor () {

    const firebaseConfig = {
      apiKey: "AIzaSyCE9VaD4xCH20ChGcy_AjnKBr-Qy0qN8ig",
      authDomain: "krania-traders.firebaseapp.com",
      projectId: "krania-traders",
      storageBucket: "krania-traders.appspot.com",
      messagingSenderId: "185778716782",
      appId: "1:185778716782:web:f9af51732875956c9be67f",
      measurementId: "G-Q6ECL3RJGS"
    };

    const app = initializeApp(firebaseConfig);
    this.db = getFirestore(app);
    getDocs(collection(this.db,'krania-traders')).then((res:any)=>{
      res.forEach((doc:any) => {
        this.data = doc.data()
      });
      this.modelData = this.data
    })
  }

  change(){
    this.enableSave = true
  }

  add(){
    this.enableSave = true
  }

  deleteObj(val:any, i:any){
    val.splice(i, 1); 
    this.enableSave = true
  }

  update(){
    let sure = confirm("Are you shure? This will update your website!")
    if(sure){
      updateDoc(doc(this.db, "krania-traders", "6s5vPMQOYAsPbocDeplJ"), this.modelData)
      .then(()=>{
        location.reload()
      });
    }
  }

  test(){
    if(this.password == "@neeraj9999"){
      this.safe = true
    }
  }
}
